.library{
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: lightcyan;
    box-shadow: 2px 2px 50px gray;
    overflow: scroll;
    h2{
        padding: 2rem;
    }

}

.library-song{
    padding: 1rem 2rem 1rem 2rem;
    display: flex;
    
    align-items: center;
    img{
        width: 30%;
    }
}